	module.exports = {"data":{"challengeNode":{"title":"Adjust the Tone of a Color","description":["<code>hsl()</code>使 CSS 更改色调更方便。给纯色添加白色可以创造更浅的色调，添加黑色可以创造更深的色调。另外，还可以通过给纯色添加灰色来同时改变颜色的深浅和明暗。回忆下<code>hsl()</code>里面的‘s’和‘l’分辨代表饱和度和亮度。饱和度代表灰色的占比，亮度代表白色和黑色的占比。这在当你有了一个基色调却需要微调时非常有用。","<hr>","页面的导航栏目前继承了<code>header</code>的<code>background-color</code>。改变<code>nav</code>元素的<code>background-color</code>，保留基色蓝绿色，调整它的色调和明暗使它具有 80% 的饱和度以及 25% 的亮度。"],"challengeType":0,"fields":{"slug":"/responsive-web-design/applied-visual-design/adjust-the-tone-of-a-color","blockName":"Applied Visual Design","tests":[{"text":"<code>nav</code>元素应该有一个使用<code>hsl()</code>属性调节蓝绿色调的<code>background-color</code>CSS 属性。","testString":"assert(code.match(/nav\\s*?{\\s*?background-color:\\s*?hsl\\(180,\\s*?80%,\\s*?25%\\)/gi), '<code>nav</code>元素应该有一个使用<code>hsl()</code>调节蓝绿色调的<code>background-color</code>CSS 属性。');"}]},"required":[],"files":{"indexhtml":{"key":"indexhtml","ext":"html","name":"index","contents":"<style>\n  header {\n    background-color: hsl(180, 90%, 35%);\n    color: #FFFFFF;\n  }\n  \n  nav {\n  \n  }\n  \n  h1 {\n    text-indent: 10px;\n    padding-top: 10px;\n  }\n  \n  nav ul {\n    margin: 0px;\n    padding: 5px 0px 5px 30px;\n  }\n  \n  nav li {\n    display: inline;\n    margin-right: 20px;\n  }\n  \n  a {\n    text-decoration: none;\n    color: inherit;\n  }\n</style>\n  \n<header>\n  <h1>FCC 中国</h1>\n  <nav>\n    <ul>\n      <li><a href=\"\">首页</a></li>\n      <li><a href=\"\">课程</a></li>\n      <li><a href=\"\">论坛</a></li>\n      <li><a href=\"\">新闻</a></li>\n      <li><a href=\"\">设置</a></li>\n    </ul>\n  </nav>\n</header>","head":"","tail":""},"indexjs":null,"indexjsx":null}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/responsive-web-design/applied-visual-design/create-a-gradual-css-linear-gradient","id":"587d78a4367417b2b2512ad5"},"slug":"/responsive-web-design/applied-visual-design/adjust-the-tone-of-a-color"}}

